import React, { Component } from 'react';
import Types from 'prop-types';

import classNames from 'classnames';

export const AlertType = {
  Info: 'info',
  Warning: 'warning',
  Error: 'error',
  Success: 'success',
};

export const AlertSize = {
  Small: 'sm',
  Large: 'lg',
};

export default class Alert extends Component {
  static propTypes = {
    type: Types.string,
    children: Types.node.isRequired,
    size: Types.oneOfType(Object.values(AlertSize)),
    dismissible: Types.bool,
    arrow: Types.bool,
  };

  static defaultProps = {
    type: AlertType.Info,
    size: AlertSize.Large,
    dismissible: false,
    arrow: false,
  };

  state = {
    dismissed: false,
  };

  dismiss() {
    this.setState({ dismissed: true });
  }

  render() {
    const { dismissed } = this.state;

    const { type, arrow, size, children, dismissible } = this.props;

    const alertClass = dismissed
      ? null
      : {
          'alert-success': type === 'success',
          'alert-info': type === 'info',
          'alert-warning': type === 'warning',
          'alert-danger': type === 'error',
          arrow,
          small: size === AlertSize.Small,
          'p-x-2': size === AlertSize.Small,
          'p-y-1': size === AlertSize.Small,
        };

    return dismissed ? null : (
      <div role="alert" className={classNames('alert', 'alert-detach', alertClass)}>
        {dismissible && (
          <button
            type="button"
            className="close"
            data-dismiss="alert"
            onClick={() => this.dismiss()}
            aria-label="Close"
          >
            <span aria-hidden="true"></span>
          </button>
        )}
        {children}
      </div>
    );
  }
}



